:root
{
  --bp-small: 576px;
  --bp-medium: 768px;
  --bp-medium-large: 992px;
  --bp-large: 1400px;

  --padding-normal: 5px 10px;
  --padding-big: 25px 10px;

  --padding-x-normal: 10px;
}

%hide_visually:not(:focus):not(:focus-whitin):not(:active)
{
  clip-path: inset(50%);
  position: absolute;
  width: 1px;
  height: 1px;
}

%make-image-responsive
{
  max-width: 100%;
  height: auto;
  display: block;
}

%reset-list
{
  padding-left: 0;
  list-style: none;
}